import React from "react";
import "./Reviews.scss";
import { Swiper, SwiperSlide } from "swiper/react";
import { Keyboard, Pagination, Navigation } from "swiper";
import { images } from "../../constants";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

const Reviews = () => {
  return (
    <>
      <div className="app__reviews">
        <Swiper
          slidesPerView={1}
          spaceBetween={30}
          keyboard={{
            enabled: true,
          }}
          pagination={{
            clickable: true,
          }}
          navigation={true}
          modules={[Keyboard, Pagination, Navigation]}
          className="mySwiper"
        >
          <SwiperSlide className="swiper-slide app__flex">
            <p>
              Firma logistyczna zapewnia mi doskona jako usug. Ich
              profesjonalizm, atwa w uyciu strona internetowa oraz uprzejmy
              personel byy na najwyszym poziomie. Usugi byy konkurencyjne
              cenowo i dostosowane do moich potrzeb. Polecam kademu, kto
              potrzebuje niezawodnej obsugi logistycznej.
            </p>
            <img src={images.face} alt="face" />
          </SwiperSlide>
          <SwiperSlide className="swiper-slide app__flex">
            <p>
            Firma transportowa, z ktr miaem do czynienia, zapewnia mi szybk i niezawodn dostaw. 
            Ich kierowcy byli bardzo profesjonalni i uprzejmi, a moje towary dotary w doskonaym stanie. 
            Gorco polecam!
            </p>
            <img src={images.face} alt="face" />
          </SwiperSlide>
          <SwiperSlide className="swiper-slide app__flex">
            <p>
            Korzystaem z usug tej firmy transportowej wielokrotnie i nigdy mnie nie zawiedli. 
            Dostawa zawsze bya punktualna, a moje towary byy bezpiecznie zapakowane. 
            Jestem bardzo zadowolony i polecam ich usugi innym.
            </p>
            <img src={images.face} alt="face" />
          </SwiperSlide>
          <SwiperSlide className="swiper-slide app__flex">
            <p>
            Firma transportowa bya nie tylko szybka i niezawodna, 
            ale take bardzo elastyczna i dostosowaa si do moich potrzeb. 
            Ich kierowcy byli kompetentni i uprzejmi, a moje towary dotary bezpiecznie na miejsce. 
            Bardzo polecam t firm transportow.
            </p>
            <img src={images.face} alt="face" />
          </SwiperSlide>
        </Swiper>
      </div>
    </>
  );
};

export default Reviews;
