import React, { useState, useEffect } from "react";
import "./WhyUs.scss";
import { AppWrap, MotionWrap } from "../../wraper";

const Whyus = () => {

  return (
    <>
<div className="app__whyus">
  <h2>Dlaczego my ?</h2>
  <p>
  Jestemy dowiadczon firm transportow, ktra posiada wiedz i umiejtnoci potrzebne do zapewnienia szybkiego i skutecznego transportu. Oferujemy zrnicowane usugi transportowe, dziki czemu klienci mog wybra najlepsz opcj dostosowan do ich potrzeb.
Wysoka jako obsugi klienta jest dla nas priorytetem - zawsze stawiamy klienta na pierwszym miejscu, oferujc szybk i profesjonaln obsug, odpowiadajc na wszystkie pytania i zapewniajc wsparcie na kadym etapie procesu transportowego.
Nasza flota pojazdw jest nowoczesna, regularnie serwisowana i sprawdzana, dziki czemu nasi klienci mog mie pewno, e ich adunki bd transportowane w bezpieczny i efektywny sposb.
Oferujemy konkurencyjne ceny, co sprawia, e jestemy atrakcyjn opcj dla klientw, ktrzy szukaj wysokiej jakoci usug transportowych w przystpnej cenie.
Jeli szukasz niezawodnej firmy transportowej, ktra oferuje szeroki zakres usug i wysok jako obsugi klienta, to zachcamy do wyboru naszej firmy transportowej.
  </p>
</div>
    </>
  );
};

export default AppWrap(
 MotionWrap( Whyus,'app__whyus'),
  'whyus',
  "app__whitebg"
  );