import React, { useState, useEffect } from "react";
import { Reviews } from '../../container'
import "./About.scss";
import { AppWrap, MotionWrap } from "../../wraper";
import Counter from "./counter";

const About = () => {
  return (
    <>
      <div className="app__about">
        <div className="app__about-text app__flex">
          <h2>O Nas</h2>
          <p>
            Importujesz/eksportujesz towar z/do Woch? Zachcamy do staej
            wsppracy  transport adunkw cao-pojazdowych lub czciowych.
            Moemy przewozi adunki do 24 ton na 38 miejscach paletowych.
          </p>
        </div>
        <div className="app__about-counters app__flex">
          <div className="app__about-counter">
            <h1><Counter number={10}/></h1>
            <p>Milionw Kilometrw</p>
          </div>
          <div className="app__about-counter">
            <h1><Counter number={120}/></h1>
            <p>Zadowolonych Klientw</p>
          </div>
          <div className="app__about-counter">
            <h1><Counter number={15}/></h1>
            <p>Lat na rynku</p>
          </div>
        </div>


        <Reviews />
      </div>
    </>
  );
};

export default AppWrap(
  MotionWrap(About, "app__about"),
  "about",
  "O Nas",
  "app__whitebg"
);
